@import url("https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");

/* modal.module.css */
.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; /* 부모를 전체 덮게끔끔 */
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  color: white;
}

.modalContentBox {
  /* width: 1140px; */
  width: 60%;
  max-width: 1140px;
  min-width: 400px;
  /* width 계속 고민해보기 줄였다 키웠다 ~ */

  display: flex;
  flex-direction: column;
  align-items: center;

  height: 80%;

  overflow-y: auto;
}

.modalTitleBox {
  font-family: "Black Han Sans", sans-serif;
  font-size: 30px;

  margin-bottom: 15px;
}

.modalImgBox {
  display: flex;
  flex-direction: row;
  justify-content: center;

  width: 100%;
}

.modalImgLeftBox {
  width: 15%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.modalImgRightBox {
  width: 15%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.modalImgLeftBox button {
  background-color: transparent; /* 배경 투명 */
  color: #333; /* 텍스트 색상 */
  font-size: 24px; /* 글자 크기 */
  width: 40px; /* 버튼 너비 */
  height: 40px; /* 버튼 높이 */
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; /* 마우스 커서 모양 */
  border-radius: 50%; /* 원형 버튼 */
  transition: all 0.3s ease; /* 호버 효과를 부드럽게 */
  outline: none; /* 포커스 시 외곽선 제거 */
}

/* 호버 및 포커스 상태 */
.modalImgLeftBox button:hover,
.modalImgLeftBox button:focus {
  background-color: #333; /* 호버시 배경색 */
  color: white; /* 텍스트 색상 변경 */
  transform: scale(1.1); /* 버튼 크기 살짝 커짐 */
}

/* 클릭 시 효과 */
.modalImgLeftBox button:active {
  transform: scale(1); /* 클릭 시 버튼 크기 원상복귀 */
}

.modalImgRightBox button {
  background-color: transparent;
  color: #333;
  font-size: 24px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.3s ease;
  outline: none;
}

/* 호버 및 포커스 상태 */
.modalImgRightBox button:hover,
.modalImgRightBox button:focus {
  background-color: #333;
  color: white;
  transform: scale(1.1);
}
.modalImgRightBox button:active {
  transform: scale(1);
}

.modalImgContentBox {
  width: 65%;
  height: 500px;

  overflow: auto; /* 내용이 넘칠 경우 스크롤 표시 */
}

.modalImgContentBox > img {
  width: 100%;
}


ul {
  list-style-type: none; 
  padding: 0; 
  margin: 0; 
}

li {
  margin-bottom: 10px; 
  font-size: 16px;
  line-height: 1.6;
  padding-left: 0; 
}


.modalReviewDetail ul {
  padding-left: 0; 
}

.modalDescriptionBox {
  width: 100%;
  display: flex;
  flex-direction: column;

  margin-bottom: 30px;
  margin-top: 30px;
}

.modalReviewBox{
  width: 100%;

  display: flex;
  flex-direction: column;
}

.modalReviewTitle{
  font-family: "Black Han Sans", sans-serif;
  font-size: 25px;
}